<template>
  <div class="big-box">
    <div class="container">
      <div class="container-header">
        <div class="container-header-left">
          <span class="span-header">全部案例</span>
          <!-- 注意：此处不能删---暂时隐藏了行业分类按钮，用写死的全部行业分类代替 -->
          <!-- <span :class="{alreadyCreated:businessSortClass==businessSort.id,firstCreated:i==0,canCreated:true}"
            v-for="(businessSort,i) in businessSortArr"
            :key="i"
          @click="businessSortClick(businessSort.id)">{{businessSort.classify_name}}</span>-->
          <span
            :class="{alreadyCreated:true,firstCreated:true,canCreated:true}"
            @click="init(1,0)"
          >全部</span>
        </div>
        <div class="search-box">
          <input type="text" class="search-input" v-model.trim="searchVal" placeholder="搜索可视化" />
          <span class="biFont icon-search" @click="init(1,'',searchVal)"></span>
        </div>
      </div>
        <div class="content-body">
      <easy-scroll :barOption="scrollOpt" :wrapStyle="wrapStyle" @xScrollToEnd="xScrollToEndFn">
          <div
            v-for="(item,num) in businessSortCases"
            :class="{businessSortCases:true,clearfix:true,firstCreated:num%4==0}"
            :key="num"
          >
            <div class="CasesHeader">
              <span class="CasesHeaderName">{{item.headline}}</span>
              <span class="CasesHeaderSite" @click.stop="dropDownClcik(num)">
                <span class="icon biFont icon-option"></span>
              </span>
              <div
                class="dropDown"
                v-click-outside="{fn:closIsDrop,arg:{msg:num}}"
                v-show="dropDownBlObj[num]"
              >
                <!-- <span>
                  <span class="icon biFont icon-edit2"></span>
                  <span>编辑</span>
                </span>
                <span>
                  <span class="icon biFont icon-delete"></span>
                  <span>删除</span>
                </span>-->
                <span @click="jumpClick(item.skip_url)">
                  <span class="icon biFont icon-preview2"></span>
                  <span>预览</span>
                </span>
              </div>
            </div>
            <div class="CasesImg">
              <img :src="item.img_url" alt @click="jumpClick(item.skip_url)" />
            </div>
          </div>
      </easy-scroll>
        </div>
    </div>
  </div>

</template>

<script>

export default {
	name: 'Visualization',
	data() {
		return {
			// 假设所有行业---暂时页面已屏蔽
			businessSortArr: ['服装行业', '电子行业', '金融行业'],
			// 控制行业按钮样式
			businessSortClass: 0,
			// 假设的案例：
			businessSortCases: [],
			// 案例下拉框显示与隐藏
			dropDownBlObj: {
				0: false,
				1: false,
				2: false,
				3: false,
				4: false,
				5: false,
				6: false,
				7: false,
				8: false,
				9: false,
				10: false,
				11: false,
				12: false,
				13: false,
				14: false,
				15: false,
				16: false,
				17: false
			},
			// 搜索框内容
			searchVal: '',
			// 滚动条样式
			scrollOpt: {
				barColor: '#343a47', //滚动条颜色
				barWidth: 6,
				railColor: '#000000', //导轨颜色
				barMarginRight: 6, //垂直滚动条距离整个容器右侧距离单位（px）
				barOpacityMin: 0.3 //滚动条非激活状态下的透明度
			},
			wrapStyle: {
				'margin-right': 'calc(0px  - 97px - 17px)'
			},
			// 滚动条防频繁请求
			xScrollToEndBl: true,
			// 当前页码
			page: 1,
			// 最大页码
			count: 1,
			classify_id: 0,
			seek: ''
		};
	},
	methods: {
		// 三个小圆点的点击事件，显示下拉框，排他
		dropDownClcik(index) {
			for (const key in this.dropDownBlObj) {
				if (key == index) {
					if (this.dropDownBlObj[key] == true) {
						this.dropDownBlObj[key] = false;
					} else {
						this.dropDownBlObj[key] = true;
					}
				} else {
					this.dropDownBlObj[key] = false;
				}
			}
		},
		// 初始化页面数据
		init(page, id, seek) {
			var url = 'api/case';
			if (page) {
				url += `?page=${page}&pagesize=16`;
			}
			if (id) {
				this.classify_id = id;
				url += `&classify_id=${id}`;
			} else {
				this.classify_id = '';
			}
			if (seek) {
				this.seek = seek;
				url += `&seek=${seek}`;
			} else {
				this.seek = '';
			}
			this.$get(url).then(res => {
				this.xScrollToEndBl = true;
				if (res.code == '000000') {
					if (res.data) {
						this.count = res.data.count;
						for (let i = 0; i < res.data.length; i++) {
							this.dropDownBlObj[i] = false;
						}
						if (page == 1) {
							this.businessSortCases = res.data.case_classify_data;
						} else {
							this.businessSortCases.concat(res.data.case_classify_data);
						}
						for (let i = 0; i < this.businessSortCases.length; i++) {
							this.dropDownBlObj[i] = false;
						}
						this.page = page;
					}
				}
			});
		},
		// 滚动翻页
		xScrollToEndFn() {
			if (this.xScrollToEndBl) {
				if (this.page + 1 > this.count) {
					return;
				}
				this.page++;
				this.xScrollToEndBl = false;
				this.init(this.page, this.classify_id, this.seek);
			}
		},
		// 点击其他地方隐藏下拉框
		closIsDrop() {
			for (const key in this.dropDownBlObj) {
				this.dropDownBlObj[key] = false;
			}
		},
		// 点击图片的跳转
		jumpClick(url) {
			function getCookie(name) {
				let arr = document.cookie.match(
					new RegExp('(^| )' + name + '=([^;]*)(;|$)')
				);
				if (arr != null) return unescape(arr[2]);
				return false;
			}
			window.open(url + '?token='+ getCookie('token'));
		},
		getBusinessSortArr() {
			var url = 'api/caseClass';
			this.$get(url).then(res => {
				if (res.code == '000000') {
					this.businessSortArr = res.data.case_classify_data;
				}
			});
		},
		businessSortClick(id) {
			this.businessSortClass = id;
			this.init(1, id);
		}
	},
	mounted() {
		this.init(1);
		this.getBusinessSortArr();
	},
	computed: {
		reactiveScrollOpt() {
			//滚动条响应式参数
			return {
				...this.scrollOpt,
				barWidth: this.vw * 0.4
			};
		}
	}
};
</script>

<style scoped>
.big-box {
  width: 100%;
  height: 100%;
  background-color: #11151f;
  user-select: none;
}
.container {
  height: 100%;
}
.container-header {
  display: flex;
  padding-left: 5.1vw;
  padding-right: 5.1vw;
  padding-top: 3.7vh;
  justify-content: space-between;
}
.container-header-left {
  display: flex;
  flex-grow: row wrap;
  align-items: center;
  height: 2.8vh;
}
.container-header .search-input {
  line-height: 3vh;
  padding-right: 1.5vw;
  width: 19.2vw;
  height: 2.8vh;
  background-color: #283247;
  padding-left: 0.4vw;
  border: solid 0.0625vw transparent;
  font-size: 0.8vw;
  color: #ffffff;
}
.container-header input::-webkit-input-placeholder {
  width: 4.4vw;
  font-size: 0.8vw;
  color: #798194;
}
.span-header {
  padding-left: 0.3vw;
  height: 0.9vw;
  font-size: 0.9vw;
  color: #ffffff;
  line-height: 0.9vw;
  text-align: center;
  border-left: 0.1vw solid #28b3fc;
}
.content-header .rename-box {
  display: inline-block;
  width: auto;
  height: 100%;
  position: relative;
}
.container-header .alreadyCreated {
  display: block;
  width: 4.2vw;
  height: 1.6vw;
  line-height: calc(1.6vw - 0.125vw);
  text-align: center;
  border: solid 0.0625vw #28b3fc;
  background-color: rgba(20, 176, 248, 0.3);
  font-size: 0.8vw;
  color: #ffffff;
}

.canCreated {
  display: block;
  width: 4.2vw;
  height: 1.6vw;
  line-height: calc(1.6vw - 0.125vw);
  border: solid 0.0625vw #222b3d;
  background-color: #222b3d;
  text-align: center;
  margin-left: 0.9vw;
  font-size: 0.8vw;
  color: #ffffff;
  cursor: pointer;
}
.canCreated:hover {
  border: solid 0.0625vw #28b3fc;
  background-color: rgba(20, 176, 248, 0.3);
}
.content-body {
  height: 83vh;
  width: 100%;
  padding: 3.1vh 5.1vw 0 5.1vw;
  color: #fff;
}
.container-header .search-box {
  position: relative;
}
.container-header .search-box input[type="text"]:focus {
  border-color: #00b9ff;
}
.container-header .search-box span {
  font-size: 0.8vw;
  position: absolute;
  right: 3%;
  top: 20%;
  color: #99a4bb;
  cursor: pointer;
}

.container-header .search-box span:hover {
  color: #3dadff;
}

.content-body .firstCreated {
  margin-left: 0;
}
.businessSortCases {
  width: 19vw;
  height: 22.5vh;
  padding: 0;
  margin: 3.7vh 0 0 1vw;
  float: left;
}
.clearfix:after {
  content: " ";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.CasesHeader {
  width: 19vw;
  height: 2.1vh;
  position: relative;
}
.CasesHeaderName {
  float: left;
  font-size: 0.8vw;
  line-height: 0.8vw;
  cursor: pointer;
}
.CasesHeaderSite:hover,
.CasesHeaderName:hover {
  color: #00b9ff;
}
.CasesHeaderSite {
  float: right;
  font-size: 0.8vw;
  line-height: 0.8vw;
  cursor: pointer;
}
.CasesImg {
  width: 19vw;
  height: 20.3vh;
}
.CasesImg img {
  width: 19vw;
  height: 20.3vh;
  cursor: pointer;
}
.dropDown {
  position: absolute;
  top: 2.1vh;
  right: -1.8vw;
  /* height: 8vh; */
  height: 3vh;
  width: 3.6vw;
  background-color: #252f40;
}
.dropDown > span {
  width: 3.6vw;
  display: inline-block;
  font-size: 0.6vw;
  line-height: 1.3vw;
  cursor: pointer;
}
.dropDown > span:hover {
  color: #3dadff;
}
.dropDown > span .icon {
  margin: 0 0.3vw 0 0.5vw;
  font-size: 0.6vw;
  vertical-align: 0;
}
</style>

